import { Record, Union } from "./fable_modules/fable-library.3.4.0/Types.js";
import { record_type, list_type, union_type } from "./fable_modules/fable-library.3.4.0/Reflection.js";
import { Document$$reflection } from "./Model.fs.js";
import { render as render_2, State as State_1, Msg$reflection as Msg$reflection_1, State$reflection as State$reflection_1 } from "./MediaPage.fs.js";
import { State as State_3, render as render_1, Msg$reflection as Msg$reflection_2 } from "./MediaPageThumbnail.fs.js";
import { render as render_3, State as State_2, Msg$reflection as Msg$reflection_3 } from "./FrontPageHeader.fs.js";
import { welcomeVideo, doc3, doc2, doc1 } from "./Resources.fs.js";
import { singleton, find, ofArray } from "./fable_modules/fable-library.3.4.0/List.js";
import { createElement } from "react";
import { Interop_reactApi } from "./fable_modules/Feliz.1.53.0/Interop.fs.js";
import { map, delay, toList } from "./fable_modules/fable-library.3.4.0/Seq.js";
import { ProgramModule_mkSimple, ProgramModule_run } from "./fable_modules/Fable.Elmish.3.1.0/program.fs.js";
import { Program_withReactSynchronous } from "./fable_modules/Fable.Elmish.React.3.0.1/react.fs.js";
import "./styles/global.scss";
import "./styles/all.min.js";



export class ShownPage extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["MediaPage", "FrontPage"];
    }
}

export function ShownPage$reflection() {
    return union_type("App.ShownPage", [], ShownPage, () => [[], []]);
}

export class State extends Record {
    constructor(Documents, MediaPageState, ShownPage) {
        super();
        this.Documents = Documents;
        this.MediaPageState = MediaPageState;
        this.ShownPage = ShownPage;
    }
}

export function State$reflection() {
    return record_type("App.State", [], State, () => [["Documents", list_type(Document$$reflection())], ["MediaPageState", State$reflection_1()], ["ShownPage", ShownPage$reflection()]]);
}

export class Msg extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["MediaPageMsg", "MediaPageThumbnailMsg", "FrontPageHeaderMsg"];
    }
}

export function Msg$reflection() {
    return union_type("App.Msg", [], Msg, () => [[["Item", Msg$reflection_1()]], [["Item", Msg$reflection_2()]], [["Item", Msg$reflection_3()]]]);
}

export function init() {
    return new State(ofArray([doc1, doc2, doc3]), new State_1(doc1), new ShownPage(1));
}

export function update(msg, state) {
    switch (msg.tag) {
        case 1: {
            return new State(state.Documents, new State_1(find((d) => (d.Id === msg.fields[0].fields[0]), state.Documents)), new ShownPage(0));
        }
        case 2: {
            return state;
        }
        default: {
            return new State(state.Documents, state.MediaPageState, new ShownPage(1));
        }
    }
}

export const fphState = new State_2("Welcome to the Trainge  Chainge\u0027s knowledgebase", "Watch the introduction video.", welcomeVideo);

export function renderMediaPageThumbnails(state, dispatch) {
    return createElement("div", {
        style: {
            paddingTop: 12,
        },
        children: Interop_reactApi.Children.toArray(Array.from(toList(delay(() => map((document$) => render_1(new State_3(document$), (arg) => {
            dispatch(new Msg(1, arg));
        }), state.Documents))))),
    });
}

export function render(state, dispatch) {
    if (state.ShownPage.tag === 0) {
        const children = singleton(render_2(state.MediaPageState, (arg_1) => {
            dispatch(new Msg(0, arg_1));
        }));
        return createElement("div", {
            children: Interop_reactApi.Children.toArray(Array.from(children)),
        });
    }
    else {
        return createElement("div", {
            children: Interop_reactApi.Children.toArray([render_3(fphState, (arg) => {
                dispatch(new Msg(2, arg));
            }), renderMediaPageThumbnails(state, dispatch)]),
        });
    }
}

ProgramModule_run(Program_withReactSynchronous("trainge-app", ProgramModule_mkSimple(init, (msg, state) => update(msg, state), (state_1, dispatch) => render(state_1, dispatch))));

